body {
  margin: 0;
  display: flex;
  place-content: center;
  font-family: 'Fraunces', 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: hsl(30, 38%, 92%);
}

.App {
  display: flex;
  flex-direction: column;
  width: 23.4rem;
  border-radius: 1rem;
  max-width: 375px;
  background-color: hsl(0, 0%, 100%);
  margin-top: 2rem;
}

.cardImg {
  width: 100%;
  position: relative;
  border-radius: 1rem 1rem 0 0;
  margin: 0;
}

.cardInfo {
  padding: 2rem;
  line-height: 1;
}
.prodType{
  font-family: 'Montserrat', sans-serif;
  font-size: small;
  letter-spacing: .3rem;
  color: hsl(228, 12%, 48%);
}
.cardInfo__title{
  font-family: 'Fraunces', 'Montserrat', sans-serif;
  color: hsl(212, 21%, 14%);
}

.cardInfo__description{
  font-family: 'Montserrat', sans-serif;
  font-size: .875rem;
  /* font-size: 14px; */
  line-height: 1.5;
  max-width: 95%;
  color: hsl(228, 12%, 48%);
  margin-bottom: 0;
}
.cardInfo__price{
  position: static;
  font-size: 1.4rem;
  color: hsl(158, 36%, 37%);
  display: flex;
  align-items: center;
  /* flex-direction: row; */
  margin-top: -1.3rem;
}
.prev-price{
  font-family: 'Montserrat', sans-serif;
  color: hsl(228, 12%, 48%);
  text-decoration: line-through;
  font-size: 14px;
  font-weight: bold;
  margin-left: 1.5rem;
}
.cardInfo__button{
  display: flex;
  flex-direction: row;
  color: hsl(0, 0%, 100%);
  background-color: hsl(158, 36%, 37%);
  align-items: center;
  justify-content: center;
  margin: 0;
  width: 100%;
  border: none;
  border-radius: 10px;
  height: 3.7rem;
  font-family: 'Montserrat', sans-serif;
  font-size: 1rem;
  font-weight: bold;
}
.btn-icon {
  margin-right: .7rem;
  stroke-width: 6px;
}

@media (min-width: 780px) {
  .App{
    display: grid;
    grid-template-columns: 1fr 1fr;
    min-width: 800px;
  }
  .cardImg{
    flex: 1 auto;
    height: auto;
    border-radius: 1rem 0 0 1rem;
  }
  .cardInfo{
    padding: 2.5rem;
  }
  .prodType{
    font-size: large;
  }
  .cardInfo__title{
    font-size: x-large;
    margin-top: -10px;
  }
  .cardInfo__description{
    font-size: large;
  }
  .cardInfo__price{
    font-size: xx-large;
  }
  .prev-price{
    font-size: large;
  }
}